import {Injectable} from '@angular/core';
import {User} from "../interfaces/user";
import {isNullOrUndefined} from "util";
import {Login} from "../interfaces/login";
import {environment} from "../../../environments/environment";
import {HttpClient} from "@angular/common/http";
import {ServiceBase} from "../classess/service-base";
import {BehaviorSubject, Observable} from "rxjs";
import {JwtResponse} from "../interfaces/jwt-response";
import {Signup} from "../interfaces/signup";

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  // user: User = null;
  public user = new BehaviorSubject<User>(null);
  isLogined: boolean = false;

  constructor(private http: HttpClient) {
  }

  attemptAuth(credentials: Login): Observable<JwtResponse> {
    console.log('apiUrl ==>', `${environment.apiUrl}`);
    return this.http.post<JwtResponse>(`${environment.apiUrl}/api/auth/login`, credentials, {headers: ServiceBase.getHeaders()});
  }

  signUp(signupInfo: Signup): Observable<string> {
    console.log('apiUrl ==>', `${environment.apiUrl}`);
    return this.http.post<string>(`${environment.apiUrl}/api/auth/signup`, signupInfo, {headers: ServiceBase.getHeaders()});
  }
}
